*{
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
  background-color: #282c34;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }

}

.botonan{
  background: rgb(4, 30, 102);
  list-style: none;
  margin: auto;
  color:#fff;
  cursor: pointer;
  padding: 10px;
}

input{
  margin: auto;
}
.botonan:hover{
  background: rgb(4, 30, 102,.6);
}

.Item{
  color:#fff;
  list-style: none;
  text-align: initial;
  padding: 10px;

  background: rgba(1,1,1,.7);
  width: 100%;
}
